@import url('https://fonts.googleapis.com/css?family=Chewy|Pacifico');
.button {    
  border-radius: 15px;
  background-color: grey;
  border: none;
  color: #FFFFFF;
  text-align: center;
  font-size: 20px;
  padding: 1px; 
  width: 150px;
  transition: all 0.5s;
  cursor: pointer;
}
  
.button span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
    text-align: center ;
    color: white;
    font-family: 'Allerta Stencil', sans-serif;
}
  
.button span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
}
  
.button:hover span {
    padding-right: 25px;
}
  
.button:hover span:after {
    opacity: 1;
    right: 0;
}
.enterSpan{
    color: white; 
}
